import React from 'react'

const AddNewContactForm = (props) => {
  return(
    <>
    <form>
      <div> name: 
        <input value = {props.nameTxt} 
               onChange={props.editNameFunc}/>
      </div>
      <div> number: 
        <input value = {props.numTxt}
               onChange={props.editNumberFunc}/>
      </div>
      <div>
        <button type="button" onClick={props.onSubmitFunc}>add</button>
      </div>
    </form>
    </>
  )
}

export default AddNewContactForm