import React from 'react'

const Phonebook = (props) => {
  const show_persons = namefilter(props.phonebook, props.filter_str)
  return (
    <>
      {show_persons.map( person =>
        <p key={person.name}> {person.name} {person.number}<button key={person.id} onClick={() => props.deleteFunc(person)}>delete</button></p>
      )}
    </>  
  )
}


const FilterByName = (props) => {
    return(
        <div> filter name with: 
          <input value = {props.filter_str} 
              onChange={props.actionFunc}/>
        </div>
      )
  }
  
const namefilter = (phonebook, sstr) => {
  return(phonebook.filter(person => person.name.includes(sstr))
  )
}
  

export {Phonebook}
export {FilterByName}
