import React, { useState, useEffect } from 'react'
import axios from 'axios'
import {Phonebook,FilterByName} from './Components/Phonebook'
import AddNewContactForm from './Components/AddNewContactForm'
import './index.css'

// const base_url = 'https://immense-shore-44135.herokuapp.com/api/persons'
const base_url = '/api/persons'

const addNewContact = (name, number) => {
  const personData = { 
    name: name,
    number: number 
  }
  const request = axios.post(base_url,personData)
  return request.then(response => response.data)
}

const updateContact = (person, number) => {
  const changedPerson = { ...person, number:number}
  const request = axios.put(base_url+'/'+person.id,changedPerson)
  return request.then(response => response.data)
}

const removeContact = (id) => {
  const request = axios.delete(base_url+'/'+id)
  return request.then(response => response.data)
}

const Notification = (props) => {
  if (props.message === null) {
    return null
  }
  const styleclass = (props.type === "error") ?  "error" :  "notification"
  return (
    <div className={styleclass}> {props.text}  </div>
  )
}

const App = () => {

  const [ message, setMessage] = useState(null)
  const [ phonebook, setPhoneBook ] = useState([]) 
  const [ newName, setNewName ] = useState('')
  const [ newNumber, setNewNumber ] = useState('')
  const [ filterStr, setFilterStr ] = useState('')
  
  const addPerson = (event) => {
    event.preventDefault()

    const person_index = phonebook.findIndex(person => person.name === newName)
    
    if (person_index === -1)
    { 
      addNewContact(newName, newNumber)
        .then( newContact => {
          setPhoneBook(phonebook.concat(newContact))
          setMessage({text:`Added ${newName}`,type:""})
          setTimeout(() => { setMessage(null) }, 5000)          
        })
        .catch(error => {
          //alert(`Failed adding ${newName}! ${error}`)
          setMessage(error._message)
          setTimeout(() => { setMessage(null) }, 5000)             
          console.log(error)
        })

    } 
    else if (phonebook[person_index].number !== newNumber)
    {
      const person_name = phonebook[person_index].name
      
      if (window.confirm(`Changing number of ${person_name}?`)) 
      {
        updateContact(phonebook[person_index], newNumber)
          .then( updatedContact => { 
            setPhoneBook( phonebook.map( person => 
              person.id !== updatedContact.id ? person : updatedContact ) ) 
            setMessage(`Updated number for ${person_name}`)
            setTimeout(() => { setMessage(null) }, 5000)
          })
          .catch(error => {
            alert('Failed updating number!')
            console.log(error)      
          })
      }
    } 
    else 
    {
      alert(`${newName} with number ${newNumber} is already added to phonebook`)
    }
    
    setNewName('')
    setNewNumber('')
  }

  const deleteContact = (contact) => {
    if (window.confirm(`Really want to remove ${contact.name} ?`)) {
      removeContact(contact.id)
        .then(() => {
          setPhoneBook(phonebook.filter(person => person.id !== contact.id))
          setMessage(`Deleted ${contact.name}`)
          setTimeout(() => { setMessage(null) }, 5000)} )
        .catch(() => {
          alert(`Something wrong - could not remove contact ${contact.name}`)
        })

    }
  }

  useEffect(() => {
    console.log('fetching phonebook')
    axios
      .get(base_url)
      .then(response => {
        console.log('fetcing ready')
        setPhoneBook(response.data)
      })
      .catch(function(error) {
        //console.log(error);
      })
  }, [])

  console.log('render', phonebook.length, 'notes')

  return (
    <div>
      <h2>Phonebook</h2>
      <Notification message={message}/>
      <FilterByName filter_str={filterStr} 
                    actionFunc={(event) => setFilterStr(event.target.value)}/>
      
      <h2>Add new contact</h2>
      <AddNewContactForm onSubmitFunc={addPerson} 
                     nameTxt = {newName}
                     numTxt = {newNumber}
                     editNameFunc={(event) => setNewName(event.target.value)} 
                     editNumberFunc={(event) => setNewNumber(event.target.value)}/>
      <h2>Numbers</h2>
      <Phonebook phonebook={phonebook} filter_str={filterStr} deleteFunc={deleteContact}/>    
    </div>
  )
}


export default App
